.map__image {
  width: 100%;
  object-fit: cover;
  display: block;
  height: 100%;
}

.map iframe {
  width: 100%;
  height: 351px;
  position: absolute;
  top: 0;
  left: 0;
}

.map {
  position: relative;
  margin-bottom: 36px;
  height: 351px;
}

.map::before {
  position: absolute;
  content: "";
  background-image: url("../img/icons/icon-map-pont.svg");
  background-repeat: no-repeat;
  width: 31px;
  height: 43px;
  background-position: 0 0;
  top: 40%;
  left: 50%;
  z-index: 20;
}

@media (max-width: 767px) {
  .map {
    height: 292px;
    margin-bottom: 30px;
  }

  .map::before {
    top: 53%;
    left: 56%;
  }

  .map iframe {
    height: 292px;
  }
}
